.header {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 20px;
  justify-content: space-between;
  align-items: center;
  margin: 10px auto;
}

.header picture {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 120px;
}

.header nav ul {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.header nav ul li a{
  text-transform: uppercase;
  color: #000000;
  font-weight: 500;
  position: relative;
}

.header nav ul li a::after{
  content: "";
  display: block;
  position: absolute;
  bottom: 0px;
  background: #000000;
  width: 0%;
  height: 1px;
  transition: 0.3s;
}

.header nav ul li a:hover:after{
  width: 80%;
}

@media(max-width: 425px){
  .header {
    justify-content: center;
  }

  .header nav ul {
  justify-content: center;
}


.header nav ul li a{
  padding: 3px 6px;
  background: #f7f7f7;
  border-radius: 5px;
}

}

