* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  text-decoration: none;
  list-style: none;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.center {
  max-width: 1024px;
  padding: 0px 10px;
  margin-left: auto;
  margin-right: auto;
}
